import React, { useRef, useEffect } from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const Banner = () => {
  const data = useStaticQuery(graphql`
    query {
      farhad: file(relativePath: { eq: "farhad.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      farhadtwo: file(relativePath: { eq: "farhadtwo.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <div className="banner">
      <div className="container">
        <div className="row">
          <div className="black-box"></div>
          <div className="black-box overlay"></div>
          <div className="side-image left">
            <Img fluid={data.farhadtwo.childImageSharp.fluid} />
          </div>
          <div className="main-text">Farhad Sharif</div>
          <div className="main-image">
            <Img fluid={data.farhad.childImageSharp.fluid} />
          </div>
        </div>
        <div className="scroll">
          <span>Scroll Down</span>
        </div>
      </div>

    </div>
  )
}

export default Banner